.cards {
    margin: 0 auto 67px auto;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(282px, 1fr));
    row-gap: 20px;
    -webkit-column-gap: 17px;
       -moz-column-gap: 17px;
            column-gap: 17px;
}

@media screen and (max-width: 597px) {
    .cards {
        margin-bottom: 48px;
    }
}


