.popup__container {
  position: relative;
  width: 100%;
  max-width: 430px;
  background-color: #FFFFFF;
  -webkit-box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
          box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  margin: auto;
}

@media screen and (max-width: 597px) {
  .popup__container {
    width: 282px;
  }
}


