.popup {
  display: -ms-flexbox;
  display: flex;
  position: fixed;
  left: 0;
  top: 0;
 
 
  -ms-flex-align: start;
 
 
      align-items: flex-start;
  visibility: hidden;
  overflow: hidden;
  width: 100%;
  height: 100%;
  opacity: 0;
  -webkit-transition: visibility 0.5s, opacity 0.5s;
  -o-transition: visibility 0.5s, opacity 0.5s;
  transition: visibility 0.5s, opacity 0.5s;
  background-color: rgba(0, 0, 0, 0.9);
}



