.profile__subtitle {
  margin: 0;
  font-weight: 400;
  font-size: 18px;
  line-height: 122%;
  max-width: 336px;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width: 773px) {
  .profile__subtitle {
    font-size: 14px;
    line-height: 17px;
    max-width: 210px;
    text-align: center;
  }
}


